<template lang="pug">
 div.dorento
    ul.tag-pagination.nav.tag-cros
      a(:href="download_api" download v-show="!isloading")    <!-- loading -->
          img(src="/static/images/download.svg" @click="download_loading" ).download      
      a(v-show="isloading")      <!-- loading  -->
        .spinner-border.spinner-border-sm.download                  
    transition(name="page" mode="out-in")
        keep-alive
            router-view(:key="$route.path")   <!-- cros -->

</template>

<script>
export default { 
   data(){
      return{
          isloading: false, //loading 
      }
   },
   computed:{
     download_api(){
        return  this.$store.state.cros.download_api ; //api
     }
   },
   methods:{
      download_loading(){ // api
         this.isloading = !this.isloading;
         let api = this.download_api ; 
         this.$http.get(api).then((res) => {
                if(res){
                    this.isloading = !this.isloading;
                    alert('');
                }else{
                    return 
                }

           })
      }

   }

   
}


</script>




