<template lang="pug">
 #app
    .main-wrapper <!--   -->
      router-view(name="sidebar")  <!-- bar -->
      TOPBAR(v-show="$route.name!= 'login'") <!-- bar  -->
      div 
        router-view(name="filter"  v-show="closeFilter") <!--    -->
      .page-wrapper <!--   -->
        .page-content(v-show="$route.name!= 'login'")
          .d-flex.justify-content-between.align-items-center.flex-wrap.grid-margin
            ul.dateChange(v-show="$route.name!= 'home'") <!--   -->
              label(@click="typeChang('q')" :class="{'date-active': dateType =='q' }") 
              label(@click="typeChang('m')" :class="{'date-active': dateType =='m' }") 
              label(@click="typeChang('')"  :class="{'date-active': dateType =='' }") 
            .datepicker-group(v-show="$route.name!= 'home'") <!--   -->
                    input.datepicker_box(type='date' v-model="date2" @change="dateChange")
                    input.datepicker_box(type='date' v-model="date1" @change="dateChange" )
                    button.send-date(@click='sendDate') <!--   -->
                          | 
                          i.fas.fa-search.ml-1         
                    .time-now.mt-2 <!--   -->
                          span.time-title   
                          i.fas.fa-long-arrow-alt-right.ml-2
                          span.ml-2 {{date2}} 
                          span.ml-2  
                          span.ml-2 {{date1}}
                    li.mt-3 <!--    -->
                      span.input-range (%)
                      input.ml-2(type="range" name="points" min="0" max="100" value="-1" v-model="range" step="5").input-range   
                      span.ml-2 {{range}}
                             
                          
        transition(name="page" mode="out-in")      <!--    -->            
            keep-alive          
                router-view
              
</template>

<script>
import TOPBAR from '@/components/menu/topbar'; 
export default {
  name: 'App',
  components:{
    TOPBAR
  },
  data:function(){
    return{
       dateType: "", //
       date1: "", //
       date2: "", //
       range: 0, //  
    }
  },
  created(){ 
    
    let theDay = new Date()   // 
    let changeDay = 30        // 
    this.date1 = theDay.toISOString().substring(0,10);  //10
    let timeStamp = theDay.setDate(theDay.getDate() - changeDay)      // theDay.getDate() 
    this.date2 = theDay.toISOString().substring(0,10);  //10
    let date1 = this.date1;
    let date2 = this.date2;
    this.$store.dispatch('cros/getTime',{date1,date2});  // cros get
    

    //
     const vm = this;
      $.ajax({
        type: "GET",
        url: `http://192.168.111.166:8070/client/data`, //
        dataType: "json",
        success: function (response) {
          let data = response;
          vm.$store.dispatch('getFilter',response); // 
          /*
          const newind = vm.data.map((item) => item.industrytype);
          const newcli = vm.data.map((item) => item.clientname);
          const newpro = vm.data.map((item) => item.productname);
          vm.ind = newind.filter((item, key, arr) => arr.indexOf(item) == key);
          vm.cli = newcli.filter((item, key, arr) => arr.indexOf(item) == key);
          vm.pro = newpro.filter((item, key, arr) => arr.indexOf(item) == key);
          vm.ind_df = newind.filter((item, key, arr) => arr.indexOf(item) == key);
          vm.cli_df = newcli.filter((item, key, arr) => arr.indexOf(item) == key);
          vm.pro_df = newpro.filter((item, key, arr) => arr.indexOf(item) == key);
          */
        },
      });
    
  },
  computed:{
     closeFilter(){
        return  this.$store.state.filter ;  // 
     },
  },
  methods:{
    typeChang(type){
      this.dateType = type;
      this.$store.dispatch('cros/timeType',type); //cros 
     // this.$store.dispatch('fb/timeType',type);
    },
    dateChange(){
      let date1 = this.date1;
      let date2 = this.date2;
      this.$store.dispatch('cros/getTime',{date1,date2});  //cros 
   
      //this.$store.dispatch('fb/getTime',{date1,date2});
    },
    sendDate(){
       //cros
      this.$store.dispatch('cros/getData'); // cros api
      this.$store.dispatch('cros/getDownload'); // cros api


      this.$store.dispatch('fb/getData');  // fb  aip
     // this.$store.dispatch('fb/getDownload'); // fb api

      this.$store.dispatch('line/getData');  // line api 
      //this.$store.dispatch('line/getDownload'); // line api
    },
    




 },
 

}
</script>

<style lang="scss"> 
  @import '@/assets/all.scss' ; //css

</style>



